import React from "react";
import styled from "styled-components";
import { useCartContext } from "../context/cart_context";
import { useUserContext } from "../context/user_context";

import { Link } from "react-router-dom";
// import address_reducer from "../reducers/address_reducer";
import { useAddressContext } from "../context/address_context";

const CartTotals = () => {
  const { total_amount, shipping_fee } = useCartContext();
  const { myUser, loginWithRedirect } = useUserContext();
  const {address} = useAddressContext();
  return (
    <Wrapper>
      <div>
        <article>
          <h5>
            subtotal: <span>â‚¹{total_amount}</span>
          </h5>
          <p>
            shipping fee : <span>â‚¹{shipping_fee}</span>
          </p>
          <hr />
          <h4>
            order total : <span>â‚¹{total_amount + shipping_fee}</span>
          </h4>
        </article>
        {myUser  ? (
          address.name ? <Link to="/checkout" className="btn">
            proceed to checkout
          </Link> : <Link to="/profile" className="btn" style={{textTransform: "capitalize", 
          fontWeight:"800", fontSize: "16px" }}>Enter your address to checkout</Link>
        ) : (
          <button type="button" className="btn" onClick={loginWithRedirect}>
            login
          </button>
        )}
      </div>
    </Wrapper>
  );
};

const Wrapper = styled.section`
  margin-top: 3rem;
  display: flex;
  justify-content: center;
  article {
    border: 1px solid var(--clr-grey-8);
    border-radius: var(--radius);
    padding: 1.5rem 3rem;
  }
  h4,
  h5,
  p {
    display: grid;
    grid-template-columns: 200px 1fr;
  }
  p {
    text-transform: capitalize;
  }
  h4 {
    margin-top: 2rem;
  }
  @media (min-width: 776px) {
    justify-content: flex-end;
  }
  .btn {
    width: 100%;
    margin-top: 1rem;
    text-align: center;
    font-weight: 700;
  }
`;

export default CartTotals;
